import React, { useState, useEffect } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";
import ChatBox from "./ChatBox/ChatBox.jsx";
const Profile = (props) => {
  axios.defaults.headers.common["auth-token"] = sessionStorage.getItem(
    "auth-token"
  );

  var [liveUsers, setLiveUsers] = useState([]);
  var [isLoaded, setLoading] = useState(false);
  var [data, setDataByHook] = useState({});
  var [notices, setNotices] = useState([]);
  var [notice, setNotice] = useState("");
  let history = useHistory();
  const setData = (info) => {
    console.log(info);
    setNotices(info.notices);
    setDataByHook(info);
    setLoading(true);
  };

  useEffect(() => {
    axios
      .get("http://localhost:5000/profile")
      .then((response) => {
        console.log(response.data);
        return response.data;
      })
      .then((info) => {
        setData(info);
      })
      .catch((err) => {
        if (err) console.log(err);
        history.push("/JoinOrCreate");
      });
  }, []);
  const Notify = (event) => {
    axios
      .post("/notify", { notice: notice })
      .then((response) => {
        console.log(response);
        setNotices((prev) => {
          return [...prev, response.data];
        });
      })
      .catch((err) => console.log(err));
    event.preventDefault();
  };

  return isLoaded ? (
    <div className="jumbotron text-center py-4">
      <h1 className="head display-3  my-0 py-0">Welcome to chat-room</h1>
      <p>Room Code : {data.room.room_id}</p>
      <ChatBox
        setLiveUsers={setLiveUsers}
        username={data.username}
        roomid={data.room.room_id}
      />
    </div>
  ) : (
    <h1>Loading</h1>
  );
};

export default Profile;
