import React from "react";
import "./Massage.css";
const Massage = (props) => {
  let isSentByMe = false;

  const trimmedName = props.myName.trim().toLowerCase();

  if (props.massage.user === trimmedName) {
    isSentByMe = true;
  }

  return isSentByMe ? (
    <div style={{ textAlign: "right" }}>
      <span>{props.massage.text}</span>
      <hr className="my-1 py-0" />
    </div>
  ) : (
    <div style={{ textAlign: "left" }}>
      <sup>
        <span className="badge badge-light">{props.massage.user}</span>
      </sup>
      <span>{props.massage.text}</span>
      <hr className="my-1 py-0" />
    </div>
  );
};

export default Massage;
