import React from "react";
import ScrollToBottom from "react-scroll-to-bottom";
import Massage from "./Massage/Massage.jsx";
import "./ShowMassages.css";
const ShowMassages = (props) => {
  return (
    <ScrollToBottom>
      {props.massageList.map((massage, index) => (
        <div className="mail" key={index}>
          <Massage massage={massage} myName={props.username} />
        </div>
      ))}
    </ScrollToBottom>
  );
};

export default ShowMassages;
