import React, { useState, useEffect } from "react";
import "./ChatBox.css";
import io from "socket.io-client";
import Input from "./Input/Input.jsx";
import ShowMassages from "./ShowMassages/ShowMassages.jsx";
let SERVER_ENDPOINT = "localhost:5000";
let socket;

let username;
let roomid;
const ChatBox = (props) => {
  const [massage, setMassage] = useState("");
  const [massageList, setMassageList] = useState([]);
  const [activeUsers, setActiveUsers] = useState([]);
  useEffect(() => {
    socket = io(SERVER_ENDPOINT);
    username = props.username;
    roomid = props.roomid;
    socket.emit("join", { name: username, room: roomid }, (e) => {
      console.log(e);
    });
    console.log(socket);
    return () => {
      socket.emit("disconnect");
      socket.off();
    };
  }, [SERVER_ENDPOINT]);

  useEffect(() => {
    socket.on("active_users", (active_users) => {
      setActiveUsers([...activeUsers, ...activeUsers]);
      props.setLiveUsers(activeUsers);
      setMassageList([...massageList, massage]);
    });
    socket.on("add_active_user", ({ name }) => {
      setActiveUsers([...activeUsers, name]);
      props.setLiveUsers(activeUsers);
      console.log(activeUsers);
    });
    socket.on("massage", (massage) => {
      setMassageList([...massageList, massage]);
    });
    socket.on("notice", (notice) => {
      ///////////////////
      console.log(notice);
    });
  }, [massageList, activeUsers]);

  const sendMassage = (event) => {
    event.preventDefault();

    if (massage !== "") {
      socket.emit("sendMassage", massage, () => setMassage(""));
    }
  };
  return (
    <div className="card text-center ChatBox-box">
      <div className="card-header">Featured</div>
      <div className="card-body">
        <ShowMassages massageList={massageList} username={username} />
      </div>
      <div className="card-footer">
        <Input
          massage={massage}
          setMassage={setMassage}
          sendMassage={sendMassage}
        />
      </div>
    </div>
  );
};

export default ChatBox;
