import React, { useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { useHistory } from "react-router-dom";

const JoinOrCreateRoom = () => {
  let history = useHistory();
  const [roomCode, setRoomCode] = useState("");
  axios.defaults.headers.common["auth-token"] = sessionStorage.getItem(
    "auth-token"
  );
  const Join = (event) => {
    axios
      .post("http://localhost:5000/join", { roomKey: roomCode })
      .then((response) => {
        console.log(response.status);
        history.push("/Profile");
      })
      .catch((err) => console.log(err));

    event.preventDefault();
  };
  const Create = (event) => {
    axios
      .post("http://localhost:5000/create_room", {})
      .then((response) => {
        console.log(response.status);
        history.push("/Profile");
      })
      .catch((err) => console.log(err));

    event.preventDefault();
  };
  return (
    <div className="container">
      <div className="jumbotron centered">
        <h1 className="display-3">Welcome To ChatRoom</h1>
        <p className="secret-text">
          Create or join a room, and chat with your gang!
        </p>
        <form onSubmit={Join}>
          <div className="form-group">
            <input
              type="text"
              className="form-control text-center joinCode"
              name="secret"
              placeholder="Please enter the room code to join..."
              value={roomCode}
              onChange={(e) => setRoomCode(e.target.value)}
            />
            <button type="submit" className="btn btn-dark joinButton">
              Join
            </button>
          </div>
        </form>
        <div>
          <span className="secret-text">Or create a room</span>
          <button className="btn btn-dark" type="submit" onClick={Create}>
            Create
          </button>
        </div>

        <p className="secret-text" className="note">
          <span>Note: </span>You can be in only one room at a time.
        </p>
      </div>
    </div>
  );
};

export default JoinOrCreateRoom;
